.Stake{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Stake .modal-stake{
    max-width: 411px;
    width: 100%;
    height: 500px;
    box-sizing: border-box;
    padding: 48px 36px;
    border-radius: 20px;
    background-color: #000836;
}
.Stake .modal-stake h3{
    color: #FFFFFF;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.16em;
    text-align: center;
}
.Stake .modal-stake .stake-number{
    width: 100%;
    border: 2px solid #E5E5E5;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 11px;
    padding: 30px 20px;
    background: #000836;
    outline: none;
    color: #FFFFFF;
}
.Stake .modal-stake button {
    max-width: 223px;
    width: 100%;
    border: 2px solid #E5E5E5;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 11px;
    background: #000836;
    margin: 30px auto;
    display: block;
    color: #FFFFFF;
    outline: none;
    cursor: pointer;
    padding: 16px 0;
}
.Stake .modal-stake p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.16em;
    color: #FFFFFF;
    margin: 0 auto;
    display: block;
    width: fit-content;
    outline: none;
    cursor: pointer;
}
